.table_tab_parnt{
    flex: 1;
    overflow-y: auto;
    overflow-x: hidden;
    // padding: 12px 12px 5px; 
    .table_tab_inr{
        min-height: 100%;
        display: flex;
        flex-direction: column;
        padding: 10px 0 0;
        background-color: #fff;
        &.tab-content{
            padding: 0;
            .tab{
                padding: 5px 12px;
            }
        }
        .tab{
            padding: 2px 12px;
        }
    }
}
.table_tab_parnt > .tab .row{
    padding-bottom: 35px;
}
/* Tab content section */
.tab{
    display:none;
    flex: 1;
}


/* When active state */
.active{
    display:block !important;
}
.wrapper.tabs .tab-container li a:focus,
.wrapper.tabs .tabs-list li.active a,
.wrapper.tabs .tab-container li:hover a{     
    background: #f8a32f;     
    color: $font-color;
    border-radius: 0;
}

.tabl_display_cnt{
    width: 120px;
    height: 50px;
    ul{
        list-style-type: none;
        padding: 0;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        width: 100%;
        height: 100%;
        margin: 0;
        li{
            color: $font-color;
            font-family: $main-font;
        }
        .tabl_num{
            font-size: 1rem;
            // padding: 13px 0;
        }
        .seat_info{
            font-size: 0.75rem;
        }
    }
}


.square_img_blk{
    position: relative;
    height: 115px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    &:after{
        position: absolute;
        content: "";
        width: 160px;
        height: 90px;
        display: inline-block;
        background: url($sprite) no-repeat;
        background-position:-319px -242px;
        margin: auto;
        z-index: -1;
    }
}

