.action_btn_parnt{
    width: 100%;
    max-height: 140px;
    overflow: auto;
    background-color: #f0eeee;
    border-top: 1px solid #ccc;
    box-shadow: 0 0px 3px 0px rgba(0, 0, 0, 0.2);
    &::-webkit-scrollbar {
        width: 3px;
    }
    svg{
        width: 24px;
        height: 24px;
        margin-bottom: 4px;
        fill: #fff;
    }
    ul{
        list-style-type: none;
        margin: 0;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        padding: 4px 0 0;
        li{
            width: 60px;
            height: 60px;
            padding: 0 2px;
            margin: 0 0 5px 0;
            text-align: center;
            a{
                position: relative;
                background: #4A4A4A;
                // background: rgb(57,57,57) !important; /* Old browsers */
                // background: -moz-linear-gradient(top, rgba(57,57,57,1) 0%, rgba(124,115,115,1) 100%) !important; /* FF3.6-15 */
                // background: -webkit-linear-gradient(top, rgba(57,57,57,1) 0%,rgba(124,115,115,1) 100%) !important; /* Chrome10-25,Safari5.1-6 */
                // background: linear-gradient(to bottom, rgba(57,57,57,1) 0%,rgba(124,115,115,1) 100%) !important; /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
                // filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#393939', endColorstr='#7c7373',GradientType=0 ) !important; /* IE6-9 */
                display: flex;
                flex-direction: column;
                align-items: center;
                width: 100%;
                height: 100%;
                border-radius: 5px;
                // padding: 33px 3px 0;
                padding: 5px 3px 0;
                box-shadow: 0 0 5px 0px rgba(0, 0, 0, 0.4); 
                text-decoration: none;
                font-size: 0.75rem;
                line-height: 0.75rem;
                color: $white-color !important; 
                cursor: pointer;
                border: none;
                word-break: break-word;
                &:hover, &:focus{
                    background: #1f1f1f;
                    // background: rgb(124,115,115); /* Old browsers */
                    // background: -moz-linear-gradient(top, rgba(124,115,115,1) 1%, rgba(57,57,57,1) 100%) !important; /* FF3.6-15 */
                    // background: -webkit-linear-gradient(top, rgba(124,115,115,1) 1%,rgba(57,57,57,1) 100%) !important; /* Chrome10-25,Safari5.1-6 */
                    // background: linear-gradient(to bottom, rgba(124,115,115,1) 1%,rgba(57,57,57,1) 100%) !important; /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
                    // filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#7c7373', endColorstr='#393939',GradientType=0 ) !important; /* IE6-9 */
                }
                &:after{
                    position: absolute;
                    content: "";
                    width: 25px;
                    height: 25px;
                    top: 5px;
                    left: 0;
                    right: 0;
                    margin: auto;
                    background-repeat: no-repeat;
                    background-size: 400px;   
                }
            }
        }
    }
}
.action_btn_hght .action_btn_parnt{
    height: 70px;
}
.action_btn_parnt ul li.unavlbl_btn a{
    opacity: 0.7;
    cursor: not-allowed !important;
    &:hover{
