.kot_detls_main_parnt{
    .kot_detls_wrap_blk{
        display: flex;
        flex-wrap: wrap;
        & > div{
            padding: 0 10px;
        }
    }
    .list_tab{
        padding-top: 6px;
        border-radius: 0;
        i{
            font-size: 1.6875rem;
        }
        span{
            display: block;
            padding-top: 2px;
            padding-bottom: 2px;
        }
    }
    .grid_tab{
        padding-top: 8px;
        border-radius: 0;
        i{
            font-size: 1.25rem;
        }
        span{
            display: block;
            padding-top: 7px;
            padding-bottom: 2px;
        }
    }
    .page_name, .sub_header_parnt .business_date, .kot_menu_detls .kot_menu_head label,.waiter_name, .kot_menu_detls .kot_qty_head ul li, .kot_menu_detls .kot_item_detls ul li span, .demo-container.dev_grd_blk .dx-datagrid-headers .dx-datagrid-table .dx-row > td, .dx-datagrid-headers .dx-header-row.dx-column-lines > td > .dx-datagrid-text-content, .dx-datagrid-headers .dx-datagrid-table .dx-row > td, .dx-datagrid-content .dx-datagrid-table .dx-row > td{
        font-size: 1.25rem;
    }
    .nav-tabs.kot_tabs .nav-link{
        font-size: 1.25rem !important;
    }
    .kot_grd_tab_ctnt {
        padding-top: 7px;
    }
    .kot_menu_detls{
        height: auto;
        max-height: 450px;
        width: 100%;
        // border-bottom: 2px solid #efefef;
        // border-left: 1px solid #efefef;
        // border-right: 1px solid #efefef;
        margin-bottom: 20px;
    }
    .kot_menu_detls .kot_menu_head{
        padding: 10px;
        height: auto;
    }
    .kot_menu_head.order_time_detls{
        border-radius: 0;
        border-top: #383838 solid 1px;
    }
    .kot_menu_detls .kot_qty_head{
        height: auto;
    }
    .kot_menu_detls .kot_item_detls{
        // border-bottom: none;
        height: 300px;
    }
    .kot_menu_detls .kot_item_detls ul{
        display: flex;
        flex-direction: column;
    }
    .kot_menu_detls .kot_item_detls ul li{
        padding: 10px;
    }
    .kot_menu_detls .kot_item_detls ul li span label{
        font-size: 1.125rem;
        line-height: 1.5rem;
        margin-top: 10px;
    }
    .dx-datagrid .dx-row > td{
        padding: 15px 5px !important;
    }
    .grd_parnt_blk.kot_grd_parnt_blk .dx-datagrid-rowsview .dx-datagrid-content tr td:last-child a:last-child{
        margin-right: 0;
    }
    .userIcon, .infoIcon, .deleteIcon, .inProgressIcon, .foodReadyIcon, .foodCompleteIcon, .acknwdgeIcon, .cancelIcon{
        width: 40px;
        height: 40px;
        top: 8px;
        border-radius: 50%;
        background-color: #fff;
        margin-right: 40px;
        padding: 0;
        i{
            width: 40px;
            height: 40px;
            position: relative;
            padding: 0;
            box-shadow: 0 0px 2px 1px rgba(0, 0, 0, 0.2);
            border-radius: 50%;
            display: flex;
        }
    }
    .inProgressIcon{
        &.inProgressActive{
            background-color: #fffade;
            .in_prg_icn{
