    .select_wrap{
        position: relative;
        user-select: none;
        &.active ul.select_ul{
            display: block !important;
        }
        &.active .default_option:before{
            top: 20px;
            transform: rotate(-225deg);
        }
        .default_option{
            background: #fff;
            border-radius: 5px;
            position: relative;
            cursor: pointer;
            &::before{
                content: "";
                position: absolute;
                top: 15px;
                right: 10px;
                width: 10px;
                height: 10px;
                border: 2px solid;
                border-color: transparent transparent #555555 #555555;
                transform: rotate(-45deg);
                z-index: 1;
            }
            li{
                padding: 0 !important;
                
            }
        }
        .option{
            width: 70px;
            height: 40px;
            border-radius: 3px;
            position: relative;
        }
        .select_ul{
            position: absolute;
            top: 42px;
            left: 0;
            width: 100%;
            background: #fff;
            border-radius: 5px;
            display: none !important;
            z-index: 2;
            box-shadow: 0 3px 3px 0px rgba(0,0,0,0.3);
